.banner {
  width: 100%;
  height: 225px;
  border-radius: 20px;
  overflow: hidden;
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  /* filtre noir */
}
.banner::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.banner__title {
  color: white;
  font-size: 2.5em;
  font-weight: 500;
  z-index: 2;
  text-align: center;
}
.banner__img {
  position: absolute;
}

@media screen and (max-width: 850px) {
  .banner__title {
    font-size: 25px;
  }
}
@media screen and (max-width: 580px) {
  .banner {
    height: 111px;
  }
  .banner__title {
    font-size: 18px;
  }
}/*# sourceMappingURL=banner.css.map */